@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC:400,700|Roboto+Condensed:400,700');

html, body{
    font-family: "Noto Sans TC", "Microsoft JhengHei", "HanHei TC", "MHei", "PingFang TC", "STHeitiTC-Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

input, textarea , button, submit {
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    border: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
}

textarea {
    resize: none;
}

a {
  color: inherit;
  text-decoration: inherit;

}

ul {
    /* list-style-type: none; */
    margin: 0;
    /* padding: 0; */
}

*{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 0;
    padding: 0;
}

.btn{
    width: 200px;
    position: relative;
    padding-right: 23px;
}

@media (min-width: 768px){

    .btn{
        padding-right: 28px;
    }
}

@media (min-width: 1024px){

    .btn{
        width: 265px;
    }
}

@media (min-width: 1280px){

    .btn{
        padding-right: 38px;
    }
}

.btn span{
    color: #707070;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 16px;
}

@media (min-width: 1280px){

    .btn span{
        font-size: 20px;
    }
}

.btn path{
    stroke-width: 1.2px;
}

@media (min-width: 1280px){

    .btn path{
        stroke-width: 1px;
    }
}

.btn .circle{
    border: 1px solid #707070;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-13px);
            transform: translateY(-13px);
}

@media (min-width: 768px){

    .btn .circle{
        width: 60px;
        height: 60px;
        -webkit-transform: translateY(-18px);
                transform: translateY(-18px);
    }
}

@media (min-width: 1280px){

    .btn .circle{
        width: 80px;
        height: 80px;
        -webkit-transform: translateY(-28px);
                transform: translateY(-28px);
    }
}

.section-title{
    position: relative;
    margin-bottom: 24px;
}

@media (min-width: 768px){

    .section-title{
        margin-bottom: 48px;
    }
}

@media (min-width: 1024px){

    .section-title{
        margin-bottom: 0;
    }
}

.section-title .grey{
    width: 130px;
    margin: auto;
}

@media (min-width: 768px){

    .section-title .grey{
        width: 200px;
    }
}

@media (min-width: 1024px){

    .section-title .grey{
        margin: 0;
    }
}

@media (min-width: 1540px){

    .section-title .grey{
        width: 300px;
    }
}

.section-title .grey img{
    width: 100%;
}

.section-title .word{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

@media (min-width: 1024px){

    .section-title .word{
        left: auto;
        right: -50px;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }
}

.section-title .word span{
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    display: block;
    text-align: center;
    letter-spacing: 2px;
}

@media (min-width: 768px){

    .section-title .word span{
        font-size: 24px;
        line-height: 32px;
    }
}

@media (min-width: 1024px){

    .section-title .word span{
        text-align: left;
    }
}

@media (min-width: 1540px){

    .section-title .word span{
        font-size: 32px;
        line-height: 40px;
    }
}

.section-title .word span:nth-child(1){
    letter-spacing: 3px;
}

.page{
    width: calc(100vw - 80px);
    margin: 0 auto;
    position: relative;
    margin-bottom: 80px;
}

@media (min-width: 768px){

    .page{
        width: calc(100vw - 160px);
        margin-bottom: 120px;
        max-width: 1440px;
    }
}

@media (min-width: 1280px){

    .page{
        margin-bottom: 240px;
    }
}

.text-grey{
    color: #707070;
}

.text-black{
    color: #555555;
}

.en{
    font-family: "Roboto Condensed", sans-serif;
    word-break: break-all;
}


